<template>
    <div @dblclick="$emit('toggle-reminder', task.id)" 
    :class="[task.reminder ? 'border-4 border-slate-100 border-l-red-600  hover:border-sky-500 hover:border-l-orange-500' : '', 'group bg-slate-100 space-y-2 px-6 py-3 mx-2 mt-4 cursor-pointer hover:bg-sky-500 transition duration-300 rounded-md']">
        
        <h3 class="text-sky-700 group-hover:text-white flex justify-between items-center text-lg">
            {{ task.text }}
            
            <i @click="$emit('delete-task', task.id)" 
            class="fa-regular fa-rectangle-xmark text-red-500 group-hover:text-gray-800"></i>
        </h3>

        <p class="text-base text-sky-700 group-hover:text-white">{{ task.day }}</p>
        <p class="text-base text-sky-700 group-hover:text-white">{{ task.reminder}}</p>

    </div>
</template>



<script>
export default {
    name: 'Task',
    props: {
        task: Object,
    },
} 
</script>

<style scope>
.reminder {
    border-left: 5px solid orange;
}
</style>