<template>
    <form @submit="onSubmit" class="m-3">
        <div class="my-4 ">
            <label>Task</label>
            <input type="text" v-model="text" name="text" placeholder="Add a Task"
            class="p-2 pl-3 w-full border-2 border-gray-300 rounded block" />
        </div>
        <div class="my-4">
            <label>Day & Time</label>
            <input
                type="text"
                v-model="day"
                name="day"
                placeholder="Add Day & Time to complete this task"
            class="p-2 pl-3 w-full border-2 border-gray-300 rounded block" />
        </div>
        <div class="my-4 flex space-x-3">
            <label>Set Reminder</label>
            <input type="checkbox" v-model="reminder" name="reminder" />
        </div>
        <input type="submit" value="Save Task" 
        class="w-full bg-gray-600 hover:bg-green-700 text-white px-3 py-1 mr-2 rounded" />
    </form>
</template>


<script>
export default {
    name: 'AddTask',
    data() {
        return {
            text: '',
            day: '',
            reminder: false
        }
    },
    methods: {
        onSubmit(e) {
            e.preventDefault()

            if (!this.text) {
                alert('Please add a task')
                return
            }

            const newTask = {
                // id: Math.floor(Math.random() * 100000),
                text: this.text,
                day: this.day,
                reminder: this.reminder
            }

            this.$emit('add-task', newTask)

        // To clear the form after adding new task
            this.text = ''
            this.day = ''
            this.reminder = false
        }
    }
}
</script>