<template>
    <header class="flex justify-between items-center mb-5">
        <h1 class="text-2xl font-bold text-green-700 mx-2">
            {{ title }}
        </h1>
        <Button v-show="homePage" @btn-click="$emit('toggle-add-task-btn')" 
        :text="showAddTask ? 'Close' : 'Add Task'"
        />
        
    </header>
</template>



<script>
import Button from './Button'

export default {
    name: 'Header',
    props: {
        title: String,
        showAddTask: Boolean,
    },
    components: {
        Button
    },
    computed: {
        homePage() {
            if(this.$route.path === '/') {
                return true
            } else {
                return false
            }
        }
    }
}
</script>

