<template>
    <button @click="onClick()"
    class='hover:bg-gray-700 bg-red-600 text-white px-3 py-1 mr-2 rounded'>
        {{ text }} 
        <!-- <i class="fa-regular fa-plus"></i> -->
    </button>
</template>


<script>
export default {
    name: 'Button',
    props: {
        text: String,
    },
    methods: {
        onClick() {
            this.$emit('btn-click');
        }
    },
}
</script>